import Main from "./components/Main";
import { useEffect } from "react";

const App = () => {
  useEffect(() => {
    const initKakao = () => {
      if (window.Kakao) {
        const kakao = window.Kakao;
        if (!kakao.isInitialized()) {
          kakao.init(process.env.REACT_APP_KAKAO_TOKEN);
        } else {
          kakao.Share.createDefaultButton({
            container: "#kakao-link-btn",
            objectType: "location",
            address: "   71",
            addressTitle: "PJ 1 ",
            content: {
              title: ",  .",
              description: "2022 12 10() 11 30",
              imageUrl:
                  "https://hmsm-wedding.github.io/invitation/images/thumb/11.jpg",
              link: {
                mobileWebUrl: "https://hmsm-wedding.github.io/invitation",
                webUrl: "https://hmsm-wedding.github.io/invitation",
              },
            },
            buttons: [
              {
                title: " ",
                link: {
                  mobileWebUrl: "https://hmsm-wedding.github.io/invitation",
                  webUrl: "https://hmsm-wedding.github.io/invitation",
                },
              },
            ],
          });
        }
      }
    };
    try {
      initKakao(); //   
    } catch (err) {
      this.errorCatch(err);
    }
  }, []);

  return <Main />;
};

export default App;
