const Message = () => {
  const ref = useRef(null);
  const isInView = useIsInViewport(ref);
  const images = [0, 1, 2, 3, 4, 5];
  const texts = [
    "    ,     ,    ,",
    "    ,     ,    ,",
    "    ,     ,     .",
    "  ,    ,   , 12  .",
    "2022 12 10.,      ,    ,   .",
    "2022 12 10.,      ,    ,   .",
  ];
  const [visibleImagesMap, setVisibleImagesMap] = useState(
    images.reduce((map, image) => {
      map[image] = false;
      return map;
    }, {})
  );

  const handleScroll = () => {
    const scrollTop = document.documentElement.scrollTop;
    const viewportHeight = window.innerHeight;

    const newVisibleImagesMap = images.reduce((map, image) => {
      map[image] = scrollTop >= image * viewportHeight;
      return map;
    }, {});
    setVisibleImagesMap(newVisibleImagesMap);
  };

  useLayoutEffect(() => {
    if (!isInView) return window.removeEventListener("pointermove", handleScroll);

    // window.addEventListener("touchstart", handleScroll);
    window.addEventListener("pointermove", handleScroll);
    handleScroll();

    document.addEventListener(
      "touchstart",
      function (e) {
        e.preventDefault();
      },
      false
    );
    document.addEventListener(
      "touchmove",
      function (e) {
        e.preventDefault();
      },
      false
    );

    return () => {
      window.removeEventListener("pointermove", handleScroll);
    };
  }, [isInView]);

  return (
    <div className="section message" ref={ref}>
      <div className="sticky">
        <div className="frame">
          {images.map((image, idx) => (
            <div
              className={classNames("image", `image_${image}`, {
                image_visible: visibleImagesMap[image],
              })}
              key={idx}
            >
              <div className="message-box">
                <div className="icon">
                  <img src={ImageUrl + "heart.png"} alt="" />
                </div>
                {texts[idx].split(",").map((text, idx) => {
                  return (
                    <p key={idx} className="message-text">
                      {text}
                    </p>
                  );
                })}
              </div>
              {idx !== images.length - 1 && (
                <div className="scroll-message">
                  <span>  </span>
                </div>
              )}
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

